<template>
  <div class="teacher-space">
    <div class="head-btns">
      <div class="more-btn" v-for="(item,index) in headBtns" :key="index">
        <div class="title">{{item.title}}</div>
        <div class="num">{{item.num}}</div>
        <div class="line-back">
          <div class="line" :class="`${item.color}-bg`"></div>
        </div>
      </div>
    </div>
    <scheduleMana :taskHead="true" :taskList="taskList"></scheduleMana>
    <div class="lessons">
      <div class="prepare">
        <div class="view-box-head">
          <span class="title">班级统计</span>
          <el-select size="mini" class="more-btn" v-model="classChoosed" placeholder="请选择">
            <el-option
              v-for="item in classOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
        <div class="personal-chart" id="classManaChart"></div>
      </div>
      <div class="excellent">
        <div class="view-box-head">
          <span class="title">资源统计</span>
          <el-select size="mini" class="more-btn" v-model="resChoosed" placeholder="请选择">
            <el-option
              v-for="item in resOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
        <div class="personal-chart" id="resManaChart"></div>
      </div>
      <div class="clear"></div>
    </div>
    <div class="resources">
      <div class="view-box-head">
        <span class="title">我的活动</span>
      </div>
      <common-table
        :columns="tableColumns"
        :datas="tableDatas"
        :pagger="false"
        th-class="table-innerhead"
        border>
        <template slot="status" slot-scope="scope">
          <span v-if="scope.value == 0" class="blue-text">未开始</span>
          <span v-if="scope.value == 1" class="green-text">进行中</span>
          <span v-if="scope.value == 2" class="red-text">已结束</span>
        </template>
        <template slot="oprate" slot-scope="scope">
          <el-button type="primary" size="mini" @click="share(scope.row)">分享</el-button>
          <el-button type="success" size="mini" @click="edit(scope.row)">编辑</el-button>
          <el-button type="danger" size="mini" @click="delete(scope.row)">删除</el-button>
        </template>
      </common-table>
      <bottom-pager :total="total" @change="pageChange"/>
    </div>
  </div>
</template>
<script>
import scheduleMana from "../../../components/personal/teacher/personalSchedule.vue";
import commonTable from "../../../components/commonTable";
import bottomPager from "../../../components/personal/bottomPager";
import "echarts/lib/chart/pie";
import "echarts/lib/component/tooltip";
import "echarts/lib/component/graphic";
import "echarts/lib/component/legend"; 
import api from '../../../utils/api';
import http from '../../../utils/http';

import cookie from '../../../utils/cookieOperate.js'
let echarts = require("echarts/lib/echarts");
export default {
  components: {
    scheduleMana,
    commonTable,
    bottomPager
  },
  data() {
    return {
      activeName: "1",
      headBtns: [
        {
          title: "我的备课",
          num: 123,
          color: "blue"
        },
        {
          title: "我的优质课程",
          num: 82,
          color: "green"
        },
        {
          title: "我的资源",
          num: 146,
          color: "grown"
        },
        {
          title: "我的班级",
          num: 3,
          color: "red"
        },
        {
          title: "我的活动",
          num: 283,
          color: "violet"
        }
      ],
      classOptions: [
        {
          value: "123",
          label: "多媒体（2）班"
        }
      ],
      resOptions: [
        {
          value: "123",
          label: "网盘资源"
        }
      ],
      resChoosed: "123",
      classChoosed: "123",
      total: 0,
      tableColumns: [
        {
          prop: "title",
          label: "活动名称"
        },
        {
          prop: "type",
          label: "活动类型"
        },
        {
          prop: "dateTime",
          label: "发起时间",
          width: 160
        },
        {
          prop: "activeClass",
          label: "活动班级"
        },
        {
          prop: "numbers",
          label: "参与人数",
          width: 77
        },
        {
          prop: "status",
          label: "状态",
          slot: true,
          width: 88
        },
        {
          prop: "oprate",
          label: "操作",
          slot: true,
          width: 220
        }
      ],
      tableDatas: [
        {
          title: "设计知识点的复盘",
          type: "头脑风暴",
          dateTime: "2019-04-08 10:26:37",
          activeClass: "多媒体（2）班",
          numbers: 52,
          status: 2
        },
        {
          title: "设计知识点的复盘",
          type: "头脑风暴",
          dateTime: "2019-09-08 10:26:37",
          activeClass: "多媒体（2）班",
          numbers: 52,
          status: 0
        },
        {
          title: "设计知识点的复盘",
          type: "头脑风暴",
          dateTime: "2019-05-22 10:26:37",
          activeClass: "多媒体（2）班",
          numbers: 52,
          status: 1
        }
      ],
      taskList: [
        {
          title: "#数据库的设计与管理",
          status: 0,
          room: "永亮楼304",
          major: "多媒体二班",
          lesson: "第二课节"
        },
        {
          title: "#数据库的设计与管理",
          status: 1,
          room: "永亮楼304",
          major: "多媒体二班",
          lesson: "第二课节"
        },
        {
          title: "#数据库的设计与管理",
          status: 2,
          room: "永亮楼304",
          major: "多媒体二班",
          lesson: "第二课节"
        },
        {
          title: "#数据库的设计与管理",
          status: 0,
          room: "永亮楼304",
          major: "多媒体二班",
          lesson: "第二课节"
        },
      ],
      username:"",
      userpwd:""
    };
  },
  created() {
    this.total = this.tableDatas.length;
  },
  mounted() {
    let params = this.$route.params.id.split("&"); 
    if(params != undefined && params != 0) {  
      this.username = params[0].split("=")[1];
      this.userpwd = params[1].split("=")[1];
      this.loginAjax(this.username, this.userpwd); 
    }
    this.drawClassManaChart();
    this.drawResManaChart();
  },
  methods: {
    getSchedule() {},
    drawClassManaChart() {
      var data = [
        {
          name: "正常",
          value: 46
        },
        {
          name: "迟到",
          value: 4
        },
        {
          name: "旷课",
          value: 2
        },
        {
          name: "早退",
          value: 1
        }
      ];
      let legend1 = [];
      let legend2 = [];
      data.forEach((element, i) => {
        if (i < data.length / 2) {
          legend1.push(element);
        } else {
          legend2.push(element);
        }
      });
      let options = {
        color: ["#FA7777", "#71C847", "#2A9EFC", "#EA9855"],
        title: {
          text: "{a|多媒体（2）班}{b|考勤统计}",
          textStyle: {
            rich: {
              a: {
                padding: 20,
                fontSize: 20,
                fontWeight: "bold"
              },
              b: {
                color: "#37A3FC",
                fontSize: 16
              }
            }
          },
          x: "40%",
          y: "8%"
        },
        graphic: {
          type: "text",
          style: {
            text: "统计时间：2019-04-08  第二课节",
            x: 180,
            y: 150,
            fill: "#999999",
            fontSize: 14
          }
        },
        grid: {
          top: "1%",
          bottom: "1%",
          left: "1%",
          right: "1%"
        },
        legend: [
          {
            top: "38%",
            left: "49%",
            itemGap: 40,
            textStyle: {
              fontSize: 14
            },
            data: legend1
          },
          {
            top: "52%",
            left: "49%",
            itemGap: 40,
            textStyle: {
              fontSize: 14
            },
            data: legend2
          }
        ],
        series: [
          {
            radius: ["69%", "90%"],
            center: ["23%", "50%"],
            type: "pie",
            hoverAnimation: false,
            animation: false,
            label: {
              normal: {
                show: true,
                formatter: "{c|{c}}\n {a|总人数}",
                rich: {
                  c: {
                    color: "#000",
                    fontSize: 50,
                    fontWeight: "bold",
                    height: 60
                  },
                  a: {
                    align: "center",
                    color: "#999999",
                    fontSize: 18,
                    height: 20
                  }
                },
                position: "center"
              }
            },
            labelLine: {
              show: false
            },
            data: [52],
            z: 1
          },
          {
            radius: ["69%", "90%"],
            center: ["23%", "50%"],
            type: "pie",
            hoverAnimation: false,
            animation: false,
            label: {
              show: false
            },
            labelLine: {
              show: false
            },
            data: data,
            z: 2
          }
        ]
      };
      let chart = echarts.init(document.getElementById("classManaChart"));
      chart.setOption(options);
    },
    drawResManaChart() {
      var data = [
        {
          name: "共有网盘资源",
          value: 126
        },
        {
          name: "最新上传资源",
          value: 7
        }
      ];
      let options = {
        color: ["#2A9EFC", "#71C847"],
        title: {
          text: "{a|网盘资源}{b|数量统计}",
          textStyle: {
            rich: {
              a: {
                padding: 20,
                fontSize: 20,
                fontWeight: "bold"
              },
              b: {
                color: "#37A3FC",
                fontSize: 16
              }
            }
          },
          x: "40%",
          y: "8%"
        },
        graphic: {
          type: "text",
          style: {
            text: "最近上传时间：2019-04-08 10:52:36",
            x: 170,
            y: 150,
            fill: "#999999",
            fontSize: 14
          }
        },
        grid: {
          top: "1%",
          bottom: "1%",
          left: "1%",
          right: "1%"
        },
        legend: {
          orient: "vertical",
          top: "38%",
          left: "49%",
          itemGap: 10,
          textStyle: {
            fontSize: 14
          },
          data: data
        },
        series: {
          radius: [0, "88%"],
          center: ["22%", "50%"],
          type: "pie",
          hoverAnimation: false,
          animation: false,
          label: {
            show: false
          },
          labelLine: {
            show: false
          },
          data: data
        }
      };
      let chart = echarts.init(document.getElementById("resManaChart"));
      chart.setOption(options);
    },
    pageChange(page) {},
    share(row) {},
    edit(row) {},
    delete(row) {},
    async loginAjax (user, psd) {
      let res = await http.post(api.login, {
        name: user,
        pwd: psd,
        from: '11'
      })
      this.loading = false
      if (res.type) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        })
      } else {
        cookie.set(api.cookie.user_infor, JSON.stringify(res.UserInfo))
        cookie.set(api.cookie.token, res.Data)
        cookie.set("loginType", "1")
        this.formLoading = false
        if (this.$route.query.relogin) {
          let tempPath = window.sessionStorage.getItem('last_path')
          if (tempPath && tempPath.indexOf('login') < 0) {
            window.location.href = tempPath
          } else {
            this.$router.push('/main/teacherSpace/teacherView/0')
          }
        } else {
          this.$router.push('/main/teacherSpace/teacherView/0')
        }
      }
    },
  }
};
</script>
<style lang="less" scoped>
.teacher-space {
  width: 925px;
  margin: 0 auto;
  .head-btns {
    width: 100%;
    height: 125px;
    display: flex;
    justify-content: space-between;
    .more-btn {
      width: 180px;
      height: 100%;
      padding: 15.8px;
      text-align: left;
      background: #fff;
      .num {
        font-size: 2.5rem;
        font-weight: 500;
      }
      .line-back {
        width: 100%;
        height: 3px;
        background: #e1e5e9;
        border-radius: 12px;
        .line {
          width: 66%;
          height: 3px;
          border-radius: 12px;
        }
      }
    }
  }
  .lessons {
    width: 100%;
    margin-top: 22px;
    .prepare {
      width: 49%;
      height: 100%;
      float: left;
      background: #fff;
      padding: 20px;
      padding-top: 0;
      .more-btn {
        width: 130px;
        user-select: none;
      }
    }
    .excellent {
      width: 49%;
      height: 100%;
      float: right;
      background: #fff;
      padding: 20px;
      padding-top: 0;
      .more-btn {
        width: 100px;
      }
    }
    .personal-chart {
      width: 100%;
      height: 200px;
    }
  }
  .resources {
    width: 100%;
    background: #fff;
    margin-top: 22px;
    padding: 20px;
    padding-top: 0;
  }
}
</style>



