<template>
  <div class="schedule-mana">
    <div class="view-box-head">
      <span class="title">
        <i class="iconfont icon-richeng"></i>&nbsp;&nbsp;日程安排
      </span>
      <span class="more-btn">查看全部</span>
    </div>
    <div class="content">
      <div class="calander">
        <div class="title">
          <span class="iconfont icon-xiangzuo" v-show="!isMin" @click="preMonth"></span>
          <span class="year-month">{{yearMonth}}</span>
          <span class="iconfont icon-jiantou" v-show="!isMax" @click="nextMonth"></span>
        </div>
        <el-date-picker
          v-model="yearMonth"
          :clearable="false"
          type="month"
          value-format="yyyy年MM月"
          @change="pickYear"
          class="pick-data"
        ></el-date-picker>
        <div class="week-box flex-box">
          <div class="flex-item">日</div>
          <div class="flex-item">一</div>
          <div class="flex-item">二</div>
          <div class="flex-item">三</div>
          <div class="flex-item">四</div>
          <div class="flex-item">五</div>
          <div class="flex-item">六</div>
        </div>
        <div class="day-box">
          <div class="day-line flex-box" v-for="(dataItem,key) in dataArr" :key="key">
            <div
              v-for="(item,index) in dataItem.children"
              :key="index"
              class="flex-item"
              :class="{'color-gray':!item.isCurMon,'cur-day':item.check}"
              @click="chooseDay(key,index)"
            >
              <div class="date-item">{{item.day}}</div>
              <div v-if="item.classNum > 0" class="class-item">{{item.classNum}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="task">
        <div class="task-title">今天共有&nbsp;{{total}}&nbsp;节课</div>
        <div class="task-box">
          <div class="task-box-card" v-for="(t,k) in taskList" :key="k">
            <div class="task-box-card-head">
              <span class="task-box-card-head-title">{{t.title}}</span>
              <span v-if="t.status==0" class="task-box-card-head-status blue-bg">未开始</span>
              <span v-if="t.status==1" class="task-box-card-head-status green-bg">进行中</span>
              <span v-if="t.status==2" class="task-box-card-head-status red-bg">已结束</span>
            </div>
            <div class="task-box-card-content">
              <span>教室：{{t.room}}</span>
              <span>班级：{{t.major}}</span>
              <span>课节：{{t.lesson}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="clear"></div>
    </div>
  </div>
</template>
<script>
let date = new Date();
export default {
  props: ["taskList", "taskHead"],
  data() {
    return {
      dataArr: [],
      yearMonth: "",
      isMax: false,
      isMin: false,
      calendarLoading: false,
      data: "",
      total: 0
    };
  },
  created() {
    this.total = this.taskList.length;
  },
  mounted() {
    this.dateHandler(date);
    this.setYearMonth(date.getMonth() + 1);
    this.$parent.getSchedule(
      `${this.parseYear()}-${this.parseMonth()}-${date.getDate()}`
    );
  },
  methods: {
    // 生成日历数组
    async dateHandler(param) {
      this.dataArr = [];
      // 根据传递的参数创建时间
      let now = new Date(param * 1);
      let monthStartDate = new Date(now.setDate(1));
      // 获取本月一号对应的星期
      let monthStartWeekDay = monthStartDate.getDay();
      // 获取日历中第一行的第一个时间
      let dyBefore = new Date(now.setDate(-monthStartWeekDay));
      // 构造日期数组
      let tempLineArr = [];
      for (let i = 0; i < 6; i++) {
        let tempItemArr = [];
        for (let j = 0; j < 7; j++) {
          dyBefore.setDate(dyBefore.getDate() + 1);
          let curMon = false;
          if (dyBefore.getMonth() == date.getMonth()) {
            curMon = true;
          }
          let checked = false;
          if (
            dyBefore.getMonth() == date.getMonth() &&
            date.getDate() === dyBefore.getDate()
          ) {
            checked = true;
          }
          tempItemArr.push({
            check: checked,
            isCurMon: curMon,
            day: dyBefore.getDate(),
            classNum: 0
          });
          this.tempClassNum = 0;
        }
        tempLineArr.push({
          children: tempItemArr
        });
      }
      this.calendarLoading = false;
      this.dataArr = tempLineArr;
    },
    //获取选择的年份
    parseYear() {
      let year = this.yearMonth.split("年")[0];
      return parseInt(year);
    },
    setYearMonth(month) {
      if (month < 10) {
        month = `0${month}`;
      }
      this.yearMonth = `${date.getFullYear()}年${month}月`;
    },
    //获取选择的月份
    parseMonth() {
      let month = this.yearMonth.split("年")[1].split("月")[0];
      if (month.startsWith("0")) {
        month = month.split("0")[1];
      }
      return parseInt(month);
    },
    //上一个月
    preMonth() {
      let month = this.parseMonth();
      month == 1 ? (month = 12) : month--;
      let tempDate = new Date(date.setMonth(date.getMonth() - 1, 1));
      this.dateHandler(tempDate);
      this.setYearMonth(month);
    },
    //下一个月
    nextMonth() {
      let month = this.parseMonth();
      month == 12 ? (month = 1) : month++;
      let tempDate = new Date(date.setMonth(date.getMonth() + 1, 1));
      this.dateHandler(tempDate);
      this.setYearMonth(month);
    },
    pickYear() {
      let year = this.parseYear();
      let month = this.parseMonth();
      date.setMonth(month - 1);
      let tempDate = new Date(date.setFullYear(year, date.getMonth(), 1));
      this.dateHandler(tempDate);
      this.setYearMonth(month);
    },
    //选择日期
    chooseDay(key, index) {
      this.dataArr.forEach(line => {
        line.children.forEach(element => {
          this.$set(element, "check", false);
        });
      });
      this.$set(
        this.dataArr[key].children[index],
        "check",
        !this.dataArr[key].children[index].check
      );

      this.day = this.dataArr[key].children[index].day;
      this.date = `${this.parseYear()}-${this.parseMonth()}-${this.day}`;
      this.$parent.getSchedule(this.date);
    },
    toPrepare(pid, csid) {
      if (pid != "")
        this.$router.push({ name: "prepareForms", query: { pid: pid } });
      else this.$router.push({ name: "prepareForms", query: { csid: csid } });
    }
  }
};
</script>
<style lang="less" scoped>
//日程安排
.schedule-mana {
  width: 100%;
  margin-top: 22px;
  background: #fff;
  padding: 20px;
  padding-top: 0;
  .content {
    width: 100%;
    margin-top: -12px;
    // 日历
    .calander {
      width: 50%;
      height: 100%;
      float: left;
      user-select: none;
      .color-gray {
        color: #ccc !important;
      }
      .flex-box {
        display: flex;
        justify-content: space-between;
        .flex-item {
          width: 12.28%;
          height: 100%;
          text-align: center;
        }
      }
      .title {
        width: 100%;
        height: 30px;
        line-height: 30px;
        font-size: 0.9rem;
        color: #aaa;
        padding: 0 20px;
        span:hover {
          cursor: pointer;
        }
        .iconfont {
          font-size: 0.6rem !important;
        }
        .icon-xiangzuo {
          float: left;
        }
        .icon-jiantou {
          float: right;
        }
      }
      .week-box {
        width: 100%;
        height: 30px;
        line-height: 30px;
        font-size: 0.9rem;
      }
      .day-box {
        width: 100%;
        height: 300px;
        .day-line {
          width: 100%;
          height: 50px;
          text-align: left;
          .flex-item {
            position: relative;
            &:hover {
              cursor: pointer;
            }
            .date-item {
              height: 100%;
              line-height: 50px;
              font-size: 0.9rem;
            }
          }
          .cur-day {
            color: #fff !important;
            background: #4ea9ff;
            border-radius: 50%;
          }
        }
      }
      .pick-data {
        position: absolute;
        width: 86px;
        top: 22px;
        left: 165px;
        input {
          height: 20px !important;
          cursor: pointer;
        }
        opacity: 0;
      }
    }
    .task {
      width: 49%;
      height: 360px;
      float: right;
      &-title {
        text-align: left;
      }
      &-box {
        width: 100%;
        height: calc(100% - 25px);
        overflow-x: hidden;
        overflow-y: scroll;
        &-card {
          width: 92%;
          border-radius: 8px;
          padding: 15px 20px;
          background: #f4f9ff;
          margin: 0 auto;
          margin-top: 20px;
          &-head {
            width: 100%;
            display: flex;
            justify-content: space-between;
            line-height: 1;
            &-status {
              padding: 2px 5px;
              border-radius: 4px;
              font-size: 0.7rem;
              color: #fff;
            }
          }
          &-content {
            width: 100%;
            display: flex;
            justify-content: space-between;
            font-size: 0.9rem;
            color: #999999;
            margin-top: 8px;
          }
        }
      }
    }
  }
}
</style>



